import React from 'react';
import './App.css';
import MainPage from './pages/MainPage';
import Sidebar from './components/sidebar/Sidebar';
import Footer from './components/footer/Footer';
import Scroller from './components/scroller/Scroller';
import { HashRouter, Routes, Route, useNavigate, useLocation } from "react-router-dom";
import SplashScreen from "./components/splashscreen/Splashscreen";
import Header from './components/header/Header';
import { useTranslation } from 'react-i18next';
import czLogo3 from './assets/images/CZ_LOGO3.png';

const NewsPage = React.lazy(() => import('./pages/news/NewsPage'));

//  component  useLocation
function AppRoutes() {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const location = useLocation();
  const [sidebarOpen, setSidebarOpen] = React.useState(false);

  // Helper Scroller 
  const scrollToTop = (smooth = false) => {
    const scrollContainer = document.querySelector('[class*="scrollContainer"]') as HTMLElement;
    if (scrollContainer) {
      scrollContainer.scrollTop = 0;
      scrollContainer.scrollLeft = 0;
    }
  };

  const scrollToElement = (element: HTMLElement, smooth = true) => {
    const scrollContainer = document.querySelector('[class*="scrollContainer"]') as HTMLElement;
    if (scrollContainer && element) {
      const elementTop = element.getBoundingClientRect().top + scrollContainer.scrollTop;
      scrollContainer.scrollTo({
        top: elementTop - 60, //  Header 
        behavior: smooth ? 'smooth' : 'auto'
      });
    }
  };

  //  hash 
  React.useEffect(() => {
    //  window.location.hash  hash
    const hash = window.location.hash.slice(1);
    
    //  hash  /news 
    if (hash && hash !== '' && !location.pathname.includes('/news')) {
      //  MainPage 
      let attemptCount = 0;
      const maxAttempts = 25;
      
      const tryScroll = () => {
        const element = document.getElementById(hash);
        attemptCount++;
        
        if (element) {
          // 
          setTimeout(() => {
            scrollToElement(element, true);
          }, 200);
        } else if (attemptCount < maxAttempts) {
          // 
          setTimeout(tryScroll, 200);
        }
      };
      
      //  500ms 
      setTimeout(tryScroll, 500);
    }
  }, [location.pathname, location.hash]);

  // 
  React.useEffect(() => {
    // 
    scrollToTop(false);
    // 
    const timer = setTimeout(() => {
      scrollToTop(false);
    }, 100);
    return () => clearTimeout(timer);
  }, [location.pathname]);

  const handleMenuClick = () => {
    setSidebarOpen(!sidebarOpen);
  };

  const scrollToSection = (sectionId: string) => {
    //  Sidebar
    setSidebarOpen(false);
    
    // 
    if (location.pathname !== '/') {
      navigate('/');
      setTimeout(() => {
        const element = document.getElementById(sectionId);
        if (element) {
          scrollToElement(element, true);
        }
      }, 100);
    } else {
      const element = document.getElementById(sectionId);
      if (element) {
        scrollToElement(element, true);
      }
    }
  };

  const menuItems = [
    { label: t('nav.home'), link: "#home", onClick: () => scrollToSection('home') },
    { label: t('nav.about'), link: "#about", onClick: () => scrollToSection('about') },
    { label: t('nav.services'), link: "#services", onClick: () => scrollToSection('services') },
    { label: t('nav.news'), link: "#/news", onClick: () => {
      //  Sidebar
      setSidebarOpen(false);
      
      // 
      scrollToTop(false);
      
      // 
      navigate('/news');
      
      // 
      setTimeout(() => {
        scrollToTop(false);
      }, 100);
    }},
    { label: t('nav.contact'), link: "#contact", onClick: () => scrollToSection('contact') }
  ];

  return (
    <Scroller>
      <>
        {/* Sidebar  */}
        <header>
          <Header 
            onMenuClick={handleMenuClick}
            isSidebarOpen={sidebarOpen}
            onScrollToSection={scrollToSection}
          />
        </header>
        <Sidebar 
          item={menuItems} 
          isOpen={sidebarOpen} 
          onClose={() => setSidebarOpen(false)} 
        />

        {/*  */}
        <div className={`app-content ${sidebarOpen ? 'pushed' : ''}`}>
          <Routes>
            <Route path="/" element={<MainPage />} />
            <Route 
              path="/news" 
              element={
                <React.Suspense fallback={<div style={{ padding: '60px 20px' }}>...</div>}>
                  <NewsPage />
                </React.Suspense>
              } 
            />
            {/* Catch-all */}
            <Route path="*" element={<MainPage />} />
          </Routes>
        </div>

        {/*  Footer */}
        <Footer />
      </>
    </Scroller>
  );
}

function App() {
  const [showSplash, setShowSplash] = React.useState(true);

  //  favicon
  React.useEffect(() => {
    const link = document.querySelector("link[rel='icon']") as HTMLLinkElement;
    if (link) {
      link.href = czLogo3;
    }
    const appleLink = document.querySelector("link[rel='apple-touch-icon']") as HTMLLinkElement;
    if (appleLink) {
      appleLink.href = czLogo3;
    }
  }, []);

  return (
    <HashRouter>
      {showSplash ? (
        <SplashScreen onFinish={() => setShowSplash(false)} />
      ) : (
        <AppRoutes />
      )}
    </HashRouter>
  );
}

export default App;
